.App {
  text-align: left;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  width: 100%;
}
label {
  text-align: center;
}
#input {
  width: 70%;
  max-width: 20rem;
  height: 2rem;
}
.button {
  width: 70%;
  max-width: 20rem;
  height: 2rem;
}
.result {
  min-height: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0;
  padding-left: 3rem;
  width: 90%;
  justify-content: flex-start;
}
li {
  margin-left: 4rem;
  padding: 0 1rem;
  min-width: 10rem;
}
